.about {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
    height: 100%;
    min-height: 751px;
    padding: 90px 0 93px 0;

    @media (max-width: 1350px) {
        padding: 90px 0 95px;
    }

    @media (max-width: 1020px) {
        padding: 90px 0 94px;
    }

    background-color: $project-black;

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 100%;
        width: 100%;

        @media (max-width: 1020px) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    &__top {
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 30px;

        @media (max-width: 1020px) {
            margin-bottom: 172px;
            flex-direction: column;
        }
    }

    &__top::after {
        display: inline-flex;
        content: '';
        position: absolute;
        width: 445px;
        height: 292px;
        top: 0;
        right: 0;


        background: transparent no-repeat center center / 445px 292px;
        background-image: url(../../img/about__deco.png);

        @media (max-width: 1350px) {
            top: 48px;
            width: 255px;
            height: 255px;
            background: transparent no-repeat center center / 255px 255px;
            background-image: url(../../img/about__deco_tablet.png);
    
        }

        @media (max-width: 1020px) {
            width: 781px;
            height: 112px;
            top: auto;
            bottom: -112px;
            right: 0;
            background: transparent no-repeat center center / 781px 112px;
            background-image: url(../../img/about__deco_phone.png);
        }
    }

    &__top-left {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 0 1 58%;

        @media (max-width: 1350px) {
            flex: 0 1 65%;
        }

        @media (max-width: 1020px) {
            margin-bottom: 31px;
            flex: 1 1 auto;
        }
    }

    &__top-header {
        margin: 0;
        margin-bottom: 15px;
        color: $white;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 400;
        font-size: 48px;
        line-height: 48px;
    }

    &__top-subtitle {
        margin: 0;
        margin-bottom: 8px;
        color: $text-gray;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
    }

    &__top-description {
        margin: 0;
        color: $text-gray;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 160%;
    }




