    &__middle {
        display: flex;
        flex-direction: row;
    }

    &__hosts-list {
        display: flex;
        grid-template-columns: repeat(4, auto);

        gap: 30px;
        margin-bottom: 30px;
        padding: 0;

        @media (max-width: 1350px) {   
            display: grid;
            grid-template-columns: repeat(2, auto);

            width: 100%;
        }

        @media (max-width: 1020px) {   
            // margin: 0 114px 30px;
        }
    }

    &__hosts-item {
        display: flex;
        flex-direction: column;
        flex: 1 1 25%;
        max-width: 255px;

        @media (max-width: 1350px) { 
            flex: 1 1 auto;
            max-width: 397px;
            width: 100%;
            height: 100%;
        }

        @media (max-width: 1020px) { 
            max-width: 255px;
        }
    }

    &__hosts-picture {
        display: flex;
        margin-bottom: 30px;
        width: 100%;
    }

    &__hosts-picture * {
        max-width: 255px;
        min-height: 255px;

        @media (max-width: 1350px) {  
            max-width: none;
        }
    }

    &__hosts-image {
        @media (max-width: 1350px) {  
            height: 255px;
            width: auto;
        }
    }

    &__hosts-name {
        overflow-wrap: anywhere;
        margin: 0;
        margin-bottom: 8px;
        max-width: 255px;
        color: $white;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;

        @media (max-width: 1350px) {   
            max-width: 100%;
        }
    }

    &__hosts-description {
        margin: 0;
        max-width: 255px;
        color: $text-gray;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;

        @media (max-width: 1350px) {   
            max-width: 100%;
        }
    }

    &__bottom {
        display: flex;
        flex-direction: column;

        @media (max-width: 1020px) {   
            // margin: 0 114px;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        max-width: 920px;
        min-height: 395px;
    }

    &__form-top {
        margin-bottom: 30px;
    }

    &__form-textarea {
        resize: none;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
        min-height: 150px;
        padding: 30px 10px 10px 32px;
        border: 1px solid $white;
        border-radius: 30px;
        background-color: transparent;
        color: $white;

    }

    &__form-textarea::placeholder {
        color: $text-gray;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 20px;
    }

    &__form-middle {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 30px;

        @media (max-width: 1350px) {  
            gap: 30px;
        }

        @media (max-width: 1020px) {
            flex-direction: column;
        }
    }

    &__form-name-input, &__form-email-input {
        max-width: 445px;
        width: 100%;
        height: 70px;
        padding: 10px 10px 10px 32px;
        border: 1px solid $white;
        border-radius: 40px;
        background-color: transparent;
        color: $white;

        @media (max-width: 1020px) {
            max-width: none;

        }
    }

    &__form-name-input::placeholder, &__form-email-input::placeholder {
        color: $text-gray;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 20px;
    }

    &__form-bottom {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__form-button {
        margin-bottom: 30px;
        padding: 24.5px 53.5px;
        background: $primary;
        border: 1px solid $primary;
        border-radius: 40px;
        color: $white;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 20px;
    }

    &__form-label {
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
        color: $white;
        font-family: 'Muller';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 12px;
    }

    &__form-checkbox {
        cursor: pointer;
        opacity: 0;
        z-index: 10;
        margin-right: 8px;
        width: 14px;
        height: 14px;
    }


    &__form-span {
        display: inline-block;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        left: 0;
        width: 14px;
        height: 14px;
        border: 1px solid $white;
    }

    &__form-span-content {
        display: inline-block;
        position: relative;
        top: 1px;
        left: 1px;
        width: 10px;
        height: 10px;
        opacity: 0;
        background: transparent no-repeat center center / 10px 8px;
        background-image: url("../../img/checkbox-arrow.svg");
        transition: opacity .3s ease-in-out;
    }

    &__form-checkbox:checked ~ &__form-span > &__form-span-content{
        opacity: 1;
    }

    &__form-label-text {
        margin: 0;
        display: flex;
        align-items: center;
    }

}